*{
margin:0px;
padding:0px;
box-sizing: border-box;
}
body
{
  font-family: "Popkins",sans-serif;
}
.header
{
  width:100%;
  height: 100vh;
  background: url("./images/bg.jpg");
  background-repeat: no-repeat; 
  background-size:cover;
  overflow: hidden;
}
 .navbar
  {
   width:100%;
   height:100px;
   position: fixed;
   top:0;
   background: #1f2235;
   z-index:1;
}
  .navbar_container
  {
    display:flex;
    height: 100px;
    align-items: center;
    justify-content: space-between;
  }
  .navbar_left-logo img
  {
    width:110px;

  }
  .navbar_right
  {
    display: flex;
    list-style-type: none;
  }
  .navbar_right li a
  {
    text-decoration: none;
    color: #fff;
    font-weight:500;
    font-size: 18px;
    padding:0 15px;
  }
  .container
  {
    width:1200px;
    margin:0 auto;
  }
  .bgMain
  {

  }
  /*  Grid System  */
  .row
  {
    display:flex;
    flex-wrap: wrap;
  }
  .col-6
  {
    width:50%;
  }
  .col-4{
    width:33.33%;
  }
.header_content
{
   display:flex;
   align-items: center;
   height:100vh;
   width:100%;
   color:#fff;

}
.header_ul
{
  list-style-type: none;
  display:flex;

}
.header_ul li 
{
  width:50px;height:50px;
  border:2px solid brown;
  border-radius: 50%;
  display:flex;
  align-items:center;
  justify-content: center;
  margin:0px 15px;
}
.header_ul li :first-child
{
  margin-left:0px;

}
.header_section h1
{
font-size:47px;
font-weight: 700;
margin-top:40px;
margin-bottom: 25px;
}
.header_section p
{
font-size:26px;
font-weight: 300;
line-height: 40px;
margin-bottom: 50px;
}
.btn
{
  outline:none;
  border:none;
  color:#fff;
  
  text-decoration: none;
}
.btn-outline
{
  border: 2px solid #ff4a57;
  padding:13px 40px;
  border-radius: 50px;
}
.btn-smart
{
  background: #ff4a57;
  padding:13px 40px;
  border-radius: 50px;
}
.play
{
  font-size:25px !important;
  padding-top:10px;
}
.banner_img
{
  padding:0px 30px;
  margin-top: 150px;
}
/* Service section  */
.service
{
background: #1f2235;
padding:100px 0;
color:#fff;
}
.common
{
  width:33.33%;
  margin: 20px auto;
  text-align:center;
}
.heading
{
  margin-bottom: 20px;
  font-weight: 500;
  color:#ff4a57;
  font-size:16px;
}
.mainHeader
{
  font-size: 32px;
  margin-bottom: 20px;
  color:#fff;
}
.mainContent
{
font-size: 16px;
color:#dfdfdf;
}
.commonBorder
{
  background:#ff4a57;
  height:3px;
  width:100px;
  margin:30px auto;

}
.serviceBox
{
  padding:40px;
  background: #23263a;
  color:#fff;
  margin: 20px;
}
.commonIcon
{
font-size:22px;color:#ff4a57;margin-bottom: 15px;
}
.service_box_header{font-size: 20px;font-weight:500;margin-bottom: 15px;}
.service_box_p
{
  font-size: 14px;color:#dfdfdf;line-height: 30px;
}
.about
{
  width:100%;background: #23263a;padding:100px 0;
}
.h-650
{
  height:650px;
}
.alignCenter {
  align-items: center;
}
.about_info
{
  color:#fff;
}
.about_info h1
{
margin-bottom: 30px;
}
.about_info_p1
{
  font-size:14px;
  margin-bottom: 20px;
}
.about_info_p2
{
  font-size: 14px;
}
.info_contacts
{
  margin-top:20px;
}
.info_contacts strong{
  color:#ff4a57;
}
.info_contacts p
{
  margin-top:3px;
}
.info_contacts .col-6{margin:10px 0;}
/* Prices*/
.prices
{
  width:100%;
  padding:100px 0;
  background: #1f2235;
}
.price
{
  background: #23263a;
  padding:25px;
  border-radius: 8px;
  margin: 35px 20px;
  color:#fff;
  text-align:center;
}
.priceHeading {
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 15px;
}
.price_rs {
  font-size: 70px;
  font-weight: 200;
  margin-bottom: 15px;
}
.price_rs span {
  font-size: 25px;
}
.price ul {
  list-style-type: none;
}
.price ul li {
  margin: 5px 0;
  border-bottom: 1px solid #3c3c3c;
  font-size: 14px;
  padding: 15px 0;
}
.price_btn {
  margin-top: 40px;
}
.contact {
  padding: 100px 0;
  background: #23263a;
}
.contactSection-logo {
  text-align: center !important;
  margin-bottom: 20px;
}
.contactSection-logo img {
  margin: 0 auto;
  width: 130px;
}
.contactSection p {
  color: #fff;
  font-size: 18px;
  margin-bottom: 20px;
  line-height: 30px;
  text-align: center !important;

}

.contactCircles {
  width: 100%;
  display: flex;
  list-style: none;
  justify-content: center;
}
.contactCircles li {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background: #1f2235;
  margin: 0 10px;
  transition: all 0.5s linear;
  cursor: pointer;
}
.contactCircles li:hover {
  background: #ff4a57;
}
/* Mobile screen */
@media (max-width: 765px) {
  .container {
    width: 100%;
    padding: 0 50px;
  }
  .col-6 {
    width: 100%;
  }
  .col-3 {
    width: 100%;
  }
  .col-4 {
    width: 100%;
  }
  .navbar__right {
    flex-direction: column;
    width: 100%;
    align-items: center;
    background: #1f2235;
    position: fixed;
    top: 0;
    left: 0;
  }
  .navbar__right li a {
    display: block;
    padding: 20px 0;
    font-size: 14px;
  }
  .toggle {
    display: flex;
  }
  .banner__img img {
    display: none;
  }
  .header__section h1 {
    font-size: 35px;
    font-weight: 700;
    margin-bottom: 20px;
  }
  .header__section p {
    font-size: 20px;
    font-weight: 300;
    line-height: 30px;
    margin-bottom: 50px;
  }
  .common {
    width: 100%;
  }
  .h-650 {
    height: auto;
  }
}
